import React, { Component } from "react";
import { Row, Col, Collapse, Modal, Button, Icon } from "antd";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../../../redux/actions";
import { buttonToHistory } from "../../../../utils/Helper";

import { CodeEditor } from "../../../../components";
import Settings from "../../../../redux/settings";
/* Import Custom Components */

const { Panel } = Collapse;

function callback(key) {
  console.log("Key: ", key);
}

const genExtra = (history, search, action = { collection: "undefined", documents: "undefined", index:0 }) => (
  <div className={"option-collapse"}
        style={{width: 180}}>
    
      <Icon
          key={0}
          type="edit"
          onClick={event => {
            // If you don't want click extra trigger collapse, you can prevent this:
            event.stopPropagation();
            buttonToHistory(
              history,
              `/cpanel/collection/${action.collection}/edit${search}`
              );
            }}
        />
    <Icon
      key={2}
      type="copy"
      onClick={event => {
        // If you don't want click extra trigger collapse, you can prevent this:
        event.stopPropagation();
        Settings.collectionId = action.index;
        buttonToHistory(
          history,
          `/cpanel/collection/${action.collection}/${action.documents}${search}`
          );
        }}
    />
    <Icon
      key={1}
      type="delete"
      onClick={event => {
        // If you don't want click extra trigger collapse, you can prevent this:
        event.stopPropagation();
      }}
    />
    <div className={"pulgar-collapse"}>
      <Icon
        key={3}
        type="right"
      />
    </div>
  </div>
);


class codeCollection extends Component {
  componentDidMount() {
    const { actions } = this.props;
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }

  state = {
    ModalText: "Content of the modal",
    visible: false,
    confirmLoading: false
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = () => {
    this.setState({
      ModalText: "The modal will be closed after two seconds",
      confirmLoading: true
    });
    setTimeout(() => {
      this.setState({
        visible: false,
        confirmLoading: false
      });
    }, 2000);
  };

  handleCancel = () => {
    console.log("Clicked cancel button");
    this.setState({
      visible: false
    });
  };

  render() {
    const { history, location } = this.props;
    const { visible, confirmLoading, ModalText } = this.state;
    let { search } = location;
    return (
      <div className="home-container">
        <Row>
          <div
            style={{
              padding: "20px 10px",
              display: "flex"
            }}
          >
            <Col span={12}>
              <Button
                className={"chip chip-fill"}
                type="primary"
                icon="database"
              >
                Collections
              </Button>
            </Col>
            <Col span={12}>
              <Button
                style={{
                  float: "right"
                }}
                onClick={this.showModal}
                className={"chip chip-on"}
                type="primary"
                icon="plus"
              >
                Collection
              </Button>
            </Col>
          </div>
        </Row>
        <Row>
          <Collapse
            className={"collapse-collections"}
            defaultActiveKey={["0"]}
            onChange={callback}
            expandIconPosition={"right"}
          >
            {Settings.collections.map((item, index) => (
              <Panel
                header={[
                  <Icon className={"icon-collapse"} type="database" />,
                  <div className={"segurity-collapse"}>
                    <Icon type="lock" />
                  </div>,
                  <div className={"title-collapse"}>
                    <strong>{item.title}</strong>
                    <small>{item.namespace}</small>
                    <small>{item.date}</small>
                  </div>
                ]}
                key={index.toString()}
                extra={genExtra(history, search,{ collection: item.title, documents: item.title, index: index })}
              >
                <CodeEditor config={item.code} />
              </Panel>
            ))}
          </Collapse>
          <br />
        </Row>
        <Modal
          title="New collection"
          visible={visible}
          onOk={this.handleOk}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
          className={Settings.theme}
          
        >
          <p>{ModalText}</p>
        </Modal>
      </div>
    );
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(codeCollection);
