import React from "react";
import { Layout } from "antd";
import Settings from "../../redux/settings";
import { withRouter } from "react-router-dom";
import qs from "qs";
import Footer from "../../components/footer/Footer";
import "./standarLayout.scss";
const { Content } = Layout;
const Logo = require("../../resources/images/logo.svg");

const StanderLayout = props => {
  let { className, children, location } = props;
  let { search } = location;
  let query = qs.parse(search.replace("?", ""));
  Settings.theme = query.theme !== "dark" && query.theme !== "light" 
                  ? "light" 
                  : query.theme;
  return (
    <Layout className={`main-layout ${className || ""} ${Settings.theme}`}>
      <Content className="main-content">{children}</Content>
      <Footer />
    </Layout>
  );
};
export default withRouter(StanderLayout);
