import React from "react";
import {
  BrowserRouter as Router,
  Link,
  Switch,
  withRouter
} from "react-router-dom";
import { RouteWithSubRoutes } from "./../../utils/Helper";
import qs from "qs";
import { Layout, Menu, Icon } from "antd";
import Settings from "../../redux/settings";
/* Import Custom Components */
import "./cpanelLayout.scss";
import Header from "../../components/header/panel/panelHeader";
import Footer from "../../components/footer/Footer";

const { Content, Sider } = Layout;
const LogoLarge = require("../../resources/images/logo.svg");
const LogoSmall = require("../../resources/images/logo-recostado.svg");

let idUrl = (url, subUrl)=>{
  for(var element in subUrl){
      /*console.log(url, subUrl[element]);*/
      if(subUrl[element].url === url || url.includes(subUrl[element].key)) { 
        url = subUrl[element].url 
        break;
      } else{ continue; };
      
  };
  return url;
}


class cpanelLayout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isToggleOn: false};

    // Este enlace es necesario para hacer que `this` funcione en el callback
    this.toggleCollapsed = this.toggleCollapsed.bind(this);
    this.bodyScroll();
  }
  bodyScroll(){
    let sider = document.getElementsByClassName("ant-layout-sider-collapsed-no")[0];
    let body = document.getElementsByTagName('body')[0];

    (!sider && body) 
    ? (body.style.overflowY = 'hidden')
    : (body.style.overflowY = 'auto') 
  }

  toggleCollapsed() {
    this.bodyScroll()
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
  }

  render() {
    let { className, location, routes } = this.props;
    let { search, pathname } = location;
    let query = qs.parse(search.replace("?", ""));
    Settings.theme = query.theme !== "dark" && query.theme !== "light" 
                    ? "light" 
                    : query.theme;
    return (
      <Router>
        <Layout className={`main-layout ${className || ""} ${Settings.theme}`}>
          <Sider  onClick={this.toggleCollapsed}
            className={`side-bar  ${!this.state.isToggleOn ? 'ant-layout-sider-collapsed-no' : 'ant-layout-sider-collapsed'}`} >
            <div className="logo">
              <img id={"logo-large"} src={LogoLarge} />
              <img id={"logo-small"} src={LogoSmall} />
              <span className="logo-title">DonecLab</span>
            </div>
            <Menu
              className={`menu-sider ${this.state.isToggleOn ? 'ant-menu-inline-collapsed ' :'ant-menu-inline-collapsed-no'}`}
              defaultSelectedKeys={[idUrl(pathname, Settings.subRouters)]}
              mode="inline"
            >
              <Menu.Item key="/cpanel">
                <Icon type="dashboard" />
                <Link to={`/cpanel/dashboard${search}`}>Dashboard</Link>
              </Menu.Item>
              <Menu.Item key="/cpanel/collections">
                <Icon type="database" />
                <Link to={`/cpanel/collections${search}`}>Collections</Link>
              </Menu.Item>
              <Menu.Item key="/cpanel/plugins">
                <Icon type="api" />
                <Link to={`/cpanel/plugins${search}`}>Plugins</Link>
              </Menu.Item>
              <Menu.Item key="/cpanel/connections">
                <Icon type="rise" />
                <Link to={`/cpanel/connections${search}`}>Connection</Link>
              </Menu.Item>
              <Menu.Item key="/cpanel/settings">
                <Icon type="setting" />
                <Link to={`/cpanel/settings${search}`}>Settings</Link>
              </Menu.Item>
            </Menu>
          </Sider>
          <Layout>
            <Header collapsed={this.state.isToggleOn} isToggleOn={this.toggleCollapsed}/>
            <Content className="main-content">
              <Switch>
                {routes.map((route, i) => (
                  <RouteWithSubRoutes key={i} {...route} />
                ))}
              </Switch>
            </Content>
            <Footer />
          </Layout>
        </Layout>
      </Router>
    );
  }
}
export default withRouter(cpanelLayout);
