import React, { Component } from "react";
import { Row, Col, Collapse, Button, Icon, Select } from 'antd';
import qs from "qs";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../../../redux/actions";
import { buttonToHistory } from "../../../../utils/Helper";

/* Import Custom Components */
import { CodeEditor } from "../../../../components";
import Settings from "../../../../redux/settings";

const { Panel } = Collapse;
const { Option } = Select;


function callback(key) {
  console.log(key);
}



const genExtra = () => (
  <div className={'option-collapse'}
        style={{width: 180}}>
    <Icon key={4} type="edit" onClick={event => {
      // If you don't want click extra trigger collapse, you can prevent this:
      event.stopPropagation();
    }}
    />
    <Icon key={3} type="save" onClick={event => {
        // If you don't want click extra trigger collapse, you can prevent this:
        event.stopPropagation();
      }}
    />
    <Icon key={1} type="delete" onClick={event => {
        // If you don't want click extra trigger collapse, you can prevent this:
        event.stopPropagation();
      }}
    />
    <div className={'pulgar-collapse'}>
      <Icon
          key={3}
          type="right"
      />
    </div>
  </div>
);

const codeAdd = (props) =>{
  const {history, match, location} = props;
  let { params } = match;
  let { search }= location;
  let collection = (params.colletionName == '' || params.colletionName == undefined) ? "Product" : params.colletionName ;
  console.log("(Collection: ", collection);
    
  return (
      <div className="home-container">
        <Row>
          <div style={{
            padding: "20px 10px",
            display: 'flex'
          }}>
            <Col span={24}>
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, `/cpanel/collections${search}`);
                }}
                className={'chip chip-fill'} type="primary" icon="database">
                Collections
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, `/cpanel/collection/${collection}${search}`);
                }}
                className={'chip chip-fill'} type="primary" icon="setting">
                {collection}
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button className={'chip chip-fill'} type="primary" icon="profile">
                {collection}
              </Button>
            </Col>
          </div>
        </Row>
        <Row>
        <Collapse
            className={'collapse-collections'}
            defaultActiveKey={['0']}
            onChange={callback}
            expandIconPosition={'right'}
          >
            <Panel key={0} header={[
                  <Icon className={'icon-collapse'} type="file" />,
                  <div className={'title-collapse'}
                        style={{
                          marginLeft: 0,
                          padding:"10px 0"}}>
                    <strong>New document: <span style={{fontWeight: 300}}>{collection}</span></strong>
                    <span>
                      <i>/App/{collection} </i>
                      </span>
                  </div>
                  ]} extra={genExtra()}>
                  <CodeEditor config={
                    Settings.collections[Settings.collectionId].code
                  } />
            </Panel>
            
          </Collapse>
          <br />
          
        </Row>
      </div>
    );
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(codeAdd);
