import React, { Component } from "react";
import { Row, Col, Collapse, Modal, Button, Icon } from "antd";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../../../redux/actions";
import { buttonToHistory } from "../../../../utils/Helper";

import { CodeEditor } from "../../../../components";
import Settings from "../../../../redux/settings";
/* Import Custom Components */

const { Panel } = Collapse;

function callback(key) {
  console.log("Key: ", key);
}

const genExtra = (history, search, action = { edit: "" }) => (
  <div className={"option-collapse"}
        style={{width: 140}}>
    
    <Icon
      key={1}
      type="delete"
      onClick={event => {
        // If you don't want click extra trigger collapse, you can prevent this:
        event.stopPropagation();
      }}
    />
    <Icon
        key={0}
        type="edit"
        onClick={event => {
          // If you don't want click extra trigger collapse, you can prevent this:
          event.stopPropagation();
          
        }}
      />
    <div className={"pulgar-collapse"}>
      <Icon
        key={3}
        type="right"
      />
    </div>
  </div>
);


class codeDocument extends Component {
  componentDidMount() {
    const { actions } = this.props;
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }


  render() {
    const {history, match, location} = this.props;
    let { params } = match;
    let { search }= location;
    let collection = (params.colletionName == '' || params.colletionName == undefined) ? "Product" : params.colletionName ;
    console.log("(Collection: ", collection);

    return (
      <div className="home-container">
        <Row>
          <div style={{
            padding: "20px 10px",
            display: 'flex'
          }}>
            <Col span={20}>
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, `/cpanel/collections${search}`);
                }}
                className={'chip chip-fill'} type="primary" icon="database">
                Collections
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, `/cpanel/collection/${collection}${search}`);
                }}
                className={'chip chip-fill'} type="primary" icon="setting">
                {collection}
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button className={'chip chip-fill'} type="primary" icon="profile">
                {collection}
              </Button>
            </Col>
            <Col span={4}>
              <Button style={{
                float: 'right'
              }}
              onClick={event => {
                // If you don't want click extra trigger collapse, you can prevent this:
                event.stopPropagation();
                buttonToHistory(history, `/cpanel/collection/${collection}/add${search}`);
              }}
              className={'chip chip-on'} type="primary" icon="plus">
                Document
              </Button>
            </Col>
          </div>
        </Row>
        <Row>
          <Collapse
            className={"collapse-collections"}
            defaultActiveKey={["0"]}
            onChange={callback}
            expandIconPosition={"right"}
          >
            {
              Settings.collections[Settings.collectionId].documents.map((item, index) => (
                <Panel
                  header={[
                    <Icon className={"icon-collapse"} type="file" />,
                    <div className={"title-collapse"}
                      style={
                        {
                          marginLeft: 0,
                          top:20,
                          height: "auto"
                        }
                      }>
                      <strong>{index}</strong>
                    </div>
                  ]}
                  key={index.toString()}
                  extra={genExtra(history, search, { edit: item.title })}
                >
                  <CodeEditor config={item} />
                </Panel>
              ))
            }
          </Collapse>
          <br />
        </Row>
        
      </div>
    );
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(codeDocument);
